.main {
  width: 1920px;
  height: 1080px;
}

.app {
  display: inline-table;
  min-height: 50%;
  border: 3px black solid;
  width: 600px;
  margin: 0;
  background: #f0f0f0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding-bottom: 15px;
}

.h1,
.InpBut {
  display: flex;
  justify-content: center;
  margin-top: 3%;
}

.Button {
  margin-left: 3%;
}

.list_text {
  background-color: #f0f0f0;
  width: 70%;
  min-height: 6%;
  margin-left: 14%;
  margin-top: 2%;
  display: inline-block;
  word-break: break-all;
  font-weight: 600;
  font-size: 25px;
}

.list_del {
  margin-left: 6px;
}

.p {
  margin-left: 40%;
  top: 75px;
  position: absolute;
  font-size: 12px;
}
